body {
  background: rgb(175, 175, 175);
}

.header {
  background: rgb(132, 168, 182);
  display: flex;
  justify-content: center;
  height: 50px;
  color: white;
}

h1 {
  margin: auto;
}
.footer {
  background: rgb(132, 168, 182);
  display: flex;
  justify-content: center;
  height: 30px;
}
a {
  float: center;
  margin: auto;
}

.world-scale {
  height: 90px;
  margin: 15px 0 20px 0;
  padding-left: 30px;
  background: rgb(145, 145, 145);
  border-radius: 10px;
  padding-bottom: 30px;
  box-shadow: 2px 1px 20px 1px rgb(141, 141, 141);
}

.world-scale > * {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart {
  background: rgb(145, 145, 145);
  border-radius: 10px;
  margin: 10px 0 10px 0;
  box-shadow: 2px 1px 20px 1px rgb(141, 141, 141);
}
